import React from "react";
import {Slide} from "react-slideshow-image";
import 'react-slideshow-image/dist/styles.css';
import {graphql, useStaticQuery} from "gatsby";
import ClientCarousel from "../../elements/ClientCarousel";

export default function Testemonials() {
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "testimonials"}}}}}
      ) {
          nodes {
            id
            title
            content
            date(formatString: "YYYY/MM/DD")
            excerpt
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(width: 100, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)
    const responsiveSettings = [
        {
            breakpoint: 800,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1
            }
        },
    ];
    return (
        <div className="md:container mx-auto md:min-h-screen flex flex-col justify-center align-middle relative pt-24 lg:pt-0 px-4">
            <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch="Testemonials">
                <span className="title-underline">
                     Testemonials
                </span>
            </h1>
            <div className="pt-8 overflow-hidden">
                <Slide slidesToShow={1} arrows={false} autoplay={true} infinite={true} indicators={false} responsive={responsiveSettings}>
                    {data.allWpPost.nodes.map((node, index) => (
                        <div key={index} className="css--outsourcing-testemonial-box relative sm:mx-8 flex flex-col justify-between h-full">
                            <p className="text-white text-justify h-full" dangerouslySetInnerHTML={{__html: node.content}}/>
                        </div>
                    ))}
                </Slide>
            </div>
            <div className="mt-24">
                <ClientCarousel slidesToShow={1} duration={0} transitionDuration={2000} arrows={false} autoplay={true} infinite={true} indicators={false}
                                responsiveSettings={[
                                    {
                                        breakpoint: 800,
                                        settings: {
                                            slidesToShow: 4,
                                            slidesToScroll: 1
                                        }
                                    },
                                ]}/>
            </div>
        </div>
    )
}
