import React from "react";
import {graphql, useStaticQuery} from "gatsby"
import {Slide} from "react-slideshow-image";

const ClientCarousel = ({slidesToShow, duration, transitionDuration, arrows, autoplay, infinite, indicators, responsiveSettings}) => {
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "clients"}}}}}

      ) {
          nodes {
            id
            title
            excerpt
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(width: 100, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)
    return (
        <div className="pt-8 overflow-hidden">
            <Slide slidesToShow={slidesToShow} duration={duration} transitionDuration={transitionDuration} arrows={arrows} autoplay={autoplay} infinite={infinite} indicators={indicators} responsive={responsiveSettings}>
                {data.allWpPost.nodes.map((node,index) => (
                    node.featuredImage.node.localFile.publicURL &&
                    <div key={index} className="mx-8 css--partners-logo">
                        <img src={node.featuredImage.node.localFile.publicURL} alt={node.title}/>
                    </div>
                ))}
            </Slide>
        </div>
    )
}
export default ClientCarousel;


